import React, { useState } from 'react';
import PropTypes from 'prop-types';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader';
import Button from '@vkontakte/vkui/dist/components/Button/Button';
import Group from '@vkontakte/vkui/dist/components/Group/Group';
import { ModalRoot, ModalCard } from '@vkontakte/vkui';
import Div from '@vkontakte/vkui/dist/components/Div/Div';
// import Avatar from '@vkontakte/vkui/dist/components/Avatar/Avatar';
import Icon56DoNotDisturbOutline from '@vkontakte/icons/dist/56/do_not_disturb_outline';
import Icon28UserCircleOutline from '@vkontakte/icons/dist/28/user_circle_outline';

import "./styles/Main.css";
import { PanelHeaderButton } from '@vkontakte/vkui';

const Main = ({ id, go, fetchedUser}) => {
	const [activeModal, setActiveModal] = useState(null)

	const auth = e => {
		// if (fetchedUser) {
		// На время разработки!!!
		if (true) {
			console.log(`FETCHED USER ${fetchedUser}`)
			go(e)
		} else {
			setActiveModal('auth-failed');
		}
	}
	// Main page UI
	return (
		<Panel id={id}>
			<PanelHeader
			left={<PanelHeaderButton>	
							<Icon28UserCircleOutline onClick={auth} data-to='account'/>
						</PanelHeaderButton>}
			>
				Профориентация
			</PanelHeader>

			<Group title="Navigation Example">
				<Div>
					<Button size="xl" level="4" onClick={go} data-to="persik" className="xl-button">
						Show me Persik, please
					</Button>
				</Div>
			</Group>
			<ModalRoot activeModal={activeModal} onClose={() => setActiveModal(null)}>
				<ModalCard
					id='auth-failed'
					onClose={() => setActiveModal(null)}
					icon={<Icon56DoNotDisturbOutline />}
					header='Вы не авторизованы'
					caption='Произошла ошибка, либо вы не авторизованы в сети ВКонтакте. Попробуйте очистить кэш в меню приложения'
					actions={[{
							title: 'Закрыть',
							mode: 'primary',
							action: () => setActiveModal(null)
						}]}
					/>
			</ModalRoot>
		</Panel>
)};

Main.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
	fetchedUser: PropTypes.shape({
		photo_200: PropTypes.string,
		first_name: PropTypes.string,
		last_name: PropTypes.string,
		city: PropTypes.shape({
			title: PropTypes.string,
		}),
	}),
};

export default Main;
